<template>
  <div>
    <el-container>
      <el-aside width="201px" class="left">
        <el-col>
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            background-color="#000"
            text-color="#fff"
            active-text-color="teal"
            unique-opened
            collapse-transition
            router
          >
            <el-menu-item index="/home">
              <i class="el-icon-setting"></i>
              <span slot="title">首页</span>
            </el-menu-item>



            <!-- 
                - 这里有必要注释一下 是怎么实现 动态 显示 右边的 菜单的
                - 首先在状态层 获取一下 后台返回 数据 也就是 gettersuser下面的 menus 
                - 我们需要循环他 因为这个下面的 如果 权限全部有的化 就是两个数组 一个是 系统设置 一个是商城管理 
                - 
             -->
            <div v-for="item in gettersuser.menus" :key="item.id">
              <!-- 如果下面有 childen 那么就代表下面还有 子分类 也就是 菜单所以 目录需要显示出来 -->
              <el-submenu :index="item.title" v-if="item.children">
                <template slot="title">
                  <i :class="item.icon"></i>
                  <span v-text="item.title"></span>
                </template>

                <!-- 进行 循环 下面的 children 渲染下面的菜单  -->
                <el-menu-item-group>
                  <el-menu-item
                    v-for="i in item.children"
                    :key="i.id"
                    :index="i.url"
                  >
                    <span v-text="i.title"></span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>

              <!-- 如果 没有 children 那就是 说 这个用户的权限 只有 子菜单 并没有 目录 只需要进行渲染菜单及可以  -->
              <el-menu-item
                :index="item.url"
                v-text="item.title"
                v-else
              ></el-menu-item>
            </div>
          </el-menu>
        </el-col>
      </el-aside>

      <el-container>
        <el-header class="top">
          <el-row class="user">
            <el-menu
              class="el-menu-demo"
              mode="horizontal"
              @select="handleSelect"
            >
              <el-submenu index="2">
                <template slot="title" id="zhanghu">我的账户</template>
                <el-menu-item index="2-1">退出登录</el-menu-item>
              </el-submenu>
            </el-menu>
          </el-row>
        </el-header>

        <!-- 面包屑导航 -->
        <el-main class="main">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
              >首页</el-breadcrumb-item
            >

            <el-breadcrumb-item>{{ $route.name }}</el-breadcrumb-item>
          </el-breadcrumb>
          <hr />

          <!-- 二级路由出口 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>

import { mapGetters, mapActions } from "vuex";

export default {
  computed: {
    ...mapGetters({
      gettersuser: "gettersuser",
    }),
  },
  
  methods: {
    ...mapActions({
      actionsuser: "actionsuser",
    }),

    handleSelect() {
      this.gettersuser.token = "";
      sessionStorage.setItem("info", "");
      this.$router.push("/login");
    },
  },
  mounted() {
    this.actionsuser(JSON.parse(sessionStorage.getItem("info")));
  },
};
</script>

<style scoped>
.el-menu-vertical-demo {
  height: 100vh;
}

.top {
  /* width: 150px; */
  position: relative;
  /* right: 0; */
  height: 40px;
  background-color: #4fc08d;
}

.quite {
  position: absolute;
  right: 0px;
  top: 8px;
}
hr {
  margin-top: 10px;
}

.el-menu-demo {
  width: 130px;
  background-color: #4fc08d;
}

.user {
  width: 220px;
  position: absolute;
  right: 0;
}
</style>


